import { ChangeDetectionStrategy, ChangeDetectorRef, Component, OnInit, ViewEncapsulation } from '@angular/core';
import { StructureSourceWarehouse } from '../../../../lib/structure/domain-api/source/structure-source.warehouse';
import { SmartComponent } from '../../../../common/cdk/smart-component';
import { ItemEntity } from '../../../../lib/structure/domain/source/item.entity';
import { StructureId } from '../../../../lib/structure/domain/structure.id';
import { ContainerTemplate } from './template/container-template';
import { ContainerTemplateArchive } from './template/container-template.archive';

@Component({
	selector: 'gui-container-source',
	template: `

		<gui-container-list-item *ngFor="let element of source"
					   [item]="element"
					   [template]="template">
		</gui-container-list-item>
		
		<gui-empty-source [items]="source">
		</gui-empty-source>

	`,
	encapsulation: ViewEncapsulation.None,
	changeDetection: ChangeDetectionStrategy.OnPush,
	host: {
		'[class.gui-container-source]': `"true"`
	}
})
export class ContainerSourceComponent extends SmartComponent implements OnInit {

	source: Array<any> = [];

	template: ContainerTemplate;

	constructor(private readonly changeDetectorRef: ChangeDetectorRef,
				private readonly structureId: StructureId,
				private readonly structureSourceWarehouse: StructureSourceWarehouse,
				private readonly containerTemplateArchive: ContainerTemplateArchive) {
		super();
	}

	ngOnInit() {
		this.structureSourceWarehouse
			.onEntities(this.structureId)
			.pipe(
				this.takeUntil()
			)
			.subscribe((items: Array<ItemEntity>) => {
				this.source = items.map(i => i.getData());
				this.changeDetectorRef.detectChanges();
			});

		this.containerTemplateArchive
			.onValue()
			.pipe(
				this.takeUntil()
			)
			.subscribe((template: ContainerTemplate) => {
				this.template = template;
				this.changeDetectorRef.detectChanges();
			});
	}

}
