import { Input, OnChanges, SimpleChanges } from '@angular/core';

import { SourceCommandDispatcher } from '../../../lib/structure/domain-api/source/source.command-dispatcher';
import { StructureId } from '../../../lib/structure/domain/structure.id';
import { PagingConfig } from '../../paging/domain-api/paging-config';
import { PagingCommandDispatcher } from '../../paging/domain-api/paging.command-dispatcher';
import { ContainerTemplate } from './source/template/container-template';
import { ContainerTemplateArchive } from './source/template/container-template.archive';
import { ListCardTemplateArchive } from './card/template/list.card-template.archive';
import { ListCardTemplate } from './card/template/list.card-template';
import { ListViewMode } from '../domain/mode/list-view-mode';
import { ListViewCommandDispatcher } from '../domain-api/list-view.command-dispatcher';
import { ListViewReadModelRootId } from '../domain-api/read/list-view.read-model-root-id';


export abstract class ContainerGateway implements OnChanges {

	@Input()
	items: Array<any>;

	@Input()
	template: ContainerTemplate;

	@Input()
	cardTemplate: ListCardTemplate;

	@Input()
	paging: PagingConfig;

	@Input()
	mode: ListViewMode;

	@Input()
	modeSelector: boolean;

	protected constructor(
		public readonly structureId: StructureId,
		public readonly listViewReadModelRootId: ListViewReadModelRootId,
		protected readonly sourceCommandService: SourceCommandDispatcher,
		protected readonly containerTemplateArchive: ContainerTemplateArchive,
		protected readonly listCardTemplateArchive: ListCardTemplateArchive,
		protected readonly structurePagingCommandDispatcher: PagingCommandDispatcher,
		protected readonly listViewCommandDispatcher: ListViewCommandDispatcher
		) {
	}

	ngOnChanges(simpleChanges: SimpleChanges) {

		if (simpleChanges.template !== undefined && simpleChanges.template.currentValue !== undefined) {
			this.containerTemplateArchive.next(this.template);
		}

		if (simpleChanges.cardTemplate !== undefined && simpleChanges.cardTemplate.currentValue !== undefined) {
			this.listCardTemplateArchive.next(this.cardTemplate);
		}

		if (simpleChanges.paging !== undefined && simpleChanges.paging.currentValue !== undefined) {

			let pagingConfig: PagingConfig;

			if (typeof this.paging === 'boolean') {
				pagingConfig = {
					enabled: this.paging
				};
			} else {
				pagingConfig = this.paging;

				// if (this.paging.displayMode !== undefined) {
				// 	this.structurePagingDisplayModeArchive.next(this.paging.displayMode);
				// }
			}

			this.structurePagingCommandDispatcher.setPaging(pagingConfig);
		}

		if (simpleChanges.mode !== undefined && simpleChanges.mode.currentValue !== undefined) {
			this.listViewCommandDispatcher.setMode(this.mode, this.listViewReadModelRootId);
		}

		if (simpleChanges.modeSelector !== undefined && simpleChanges.modeSelector.currentValue !== undefined) {
			this.listViewCommandDispatcher.toggleSelector(this.modeSelector, this.listViewReadModelRootId);
		}

		/**
		 * Setting source should be last step
		 */
		if (simpleChanges.items) {
			this.sourceCommandService.setOrigin(this.items, this.structureId);
		}
	}
}
