import { ChangeDetectionStrategy, ChangeDetectorRef, Component, OnInit, ViewEncapsulation } from '@angular/core';

import { StructureSourceWarehouse } from '../../../../lib/structure/domain-api/source/structure-source.warehouse';
import { SmartComponent } from '../../../../common/cdk/smart-component';
import { ItemEntity } from '../../../../lib/structure/domain/source/item.entity';
import { StructureId } from '../../../../lib/structure/domain/structure.id';
import { ListCardTemplate } from './template/list.card-template';
import { ListCardTemplateArchive } from './template/list.card-template.archive';

@Component({
	selector: 'gui-list-container-card',
	template: `

		<gui-list-card-item *ngFor="let element of items"
								 [item]="element"
								 [template]="cardTemplate">
		</gui-list-card-item>

		<gui-empty-source [items]="items">
		</gui-empty-source>

	`,
	encapsulation: ViewEncapsulation.None,
	changeDetection: ChangeDetectionStrategy.OnPush,
	host: {
		'[class.gui-list-container-card]': `"true"`
	}
})
export class ListContainerCardComponent extends SmartComponent implements OnInit {

	items: Array<any> = [];

	cardTemplate: ListCardTemplate;

	constructor(private readonly changeDetectorRef: ChangeDetectorRef,
				private readonly structureId: StructureId,
				private readonly structureSourceWarehouse: StructureSourceWarehouse,
				private readonly listCardTemplateArchive: ListCardTemplateArchive) {
		super();
	}

	ngOnInit() {
		this.structureSourceWarehouse
			.onEntities(this.structureId)
			.pipe(
				this.takeUntil()
			)
			.subscribe((items: Array<ItemEntity>) => {
				this.items = items.map(i => i.getData());
				this.changeDetectorRef.detectChanges();
			});

		this.listCardTemplateArchive
			.onValue()
			.pipe(
				this.takeUntil()
			)
			.subscribe((template: ListCardTemplate) => {
				this.cardTemplate = template;
				this.changeDetectorRef.detectChanges();
			});
	}

}
